<template>
  <el-form :model="form" :rules="rules" label-width="100px" ref="form">
    <el-form-item class="email-input" prop="email" :label="$t('login.email') + ':'" maxlength="64">
      <el-input v-model="form.email" clearable />
    </el-form-item>

    <el-form-item prop="content" :label="$t('login.comments') + ':'">
      <el-input
        v-model="form.content"
        type="textarea"
        rows="6"
        resize="none"
        :placeholder="$t('login.feedback.thanks_tip')"
        maxlength="500"
        show-word-limit
        @keyup.native.enter="$emit('validate', form)" />
    </el-form-item>
  </el-form>
</template>

<script>
import { mixinValidateForm } from '@/mixins/form-validation';
import validator from '@/utils/validators/rules-validation';

export default {
  mixins: [mixinValidateForm],
  data() {
    return {
      form: {
        email: '',
        content: '',
      },
      rules: {
        email: validator.rules('requirement', 'email'),
        content: validator.rules('requirement'),
      },
    };
  },
};
</script>

