<template>
  <div class="feedback-page">

    <!-- 订阅成功后显示内容 -->
    <success-message class="success-msg" v-if="completed" :title="messageTitle" />

    <template v-else>
      <img
        :src="logo"
        class="logo"
        :class="{'logo-anta':appName === 'anta'}"
        :alt="$t('login.index.logo_alt')"
        @click="$router.push({ name: 'Login' })" />
      <div class="feedback-content">
        <h1 class="feedback-title">{{ $t('login.feedback.collaboration_req_com') }}</h1>

        <form-feedback @validate="submit" ref="formFeedback" />

        <el-button type="primary" @click="validateForm">{{ $t('public.send') }}</el-button>

        <back-login style="margin-left: 100px;" />
      </div>
    </template>
  </div>
</template>

<script>
import i18n from 'i18next';
import mixinLoading from '@/mixins/loading';
import { saveCooperationMessage } from '@/api/service';
import envs from '@/utils/envs';
import SuccessMessage from '../components/SuccessMessage';
import BackLogin from '../components/BackLogin';
import FormFeedback from './components/FormFeedback';


export default {
  components: {
    SuccessMessage,
    FormFeedback,
    BackLogin,
  },
  mixins: [mixinLoading],
  data() {
    return {
      completed: false,
      messageTitle: i18n.t('login.feedback.collaboration_req_suc'),
    };
  },
  computed: {
    logo() {
      return envs.VUE_APP_COOPERATION === 'anta' ? require('@/assets/img/login/logo-anta.png')
        : require('@/assets/img/login/logo-register.png');
    },
    appName() {
      return envs.VUE_APP_COOPERATION;
    },
  },
  methods: {
    validateForm() {
      this.$refs.formFeedback.validateForm();
    },
    async submit(form) {
      // 当发送请求前，添加 Loading 状态
      this.startLoading();

      try {
        await saveCooperationMessage({ ...form, email: form.email.toLowerCase() });
        this.completed = true;
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.feedback-page {
  padding: 0 40px 40px;
  min-height: 480px;

  .logo {
    margin: 40px 0 40px -16px;
    height: 24px;
    cursor: pointer;
    &.logo-anta {
      height: 19px;
      margin: 44px 0 40px 0;
    }
  }

  .feedback-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 680px;

    .feedback-title {
      margin: 0 0 20px;
      font-size: 16px;
    }
    .el-button {
      width: 320px;
      margin: 10px 0 20px 100px;
    }
  }
  .success-msg {
    margin-top: 120px;
    height: 240px;
  }
}
</style>
